import classnames from 'classnames'

import { front } from './styled'

const embedCode = (form) => {
  return (form && form[0])
    ? { __html: form[0].embed }
    : null
}

const classes = (props) => {
  return classnames(
    props.className,
    front.base,
    props.attributes.boxed ? front.boxed : null,
  )
}

const Save = props => (
  <div className={classes(props)}>
    <div dangerouslySetInnerHTML={embedCode(props.attributes.form)} />
  </div>
)

export { Save }